import axios from "axios";
let ISDEV = false;
//
if (process.env.VUE_APP_AXIOSHOST) {
    ISDEV = true;
    axios.defaults.baseURL = process.env.VUE_APP_AXIOSHOST
}
//

axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';

axios.interceptors.response.use(resp => {
    return resp;
}, err => {
    if (err && err.response.status == 401) {
        location.reload();
    }
    return Promise.reject(err);
});

function LeaveGetBalances () {
    return axios.post('/Leave/GetBalances' ).then(res => {
        return res
    })
}

// date 30-Sep-2020
// code 07
function LeaveBalanceWithDate (date, code) {
    return axios.get(`/EmployeeLeaveBalance/102?dateTo=${date}&leaveCode=${code}`).then(res => {
        return res;
    })
}

export default {
    LeaveGetBalances,
    LeaveBalanceWithDate
}