<script>
  import Remote from '../remote/index'
  import moment from 'moment';
  class Leave {
    constructor(title, date) {
      this.title = title;
      this.date = date;
      this.jsDate = null;
      this.days = 0;
      this.hours = 0;
      this.entitled = 0;
      this.show = false;
    }
  }

  export default {
    data() {
      return {
        loading: true,
        annual: null,
        longservice: null,
        sick: null,
        error: null
      };
    },

    created() {
      this.reset();

      // this.$ajax
      //   .post("/Leave/GetBalances")
      Remote.LeaveGetBalances()
        .then(resp => {
          if (resp.data.error) {
            return console.log(data.error);
          }

          let data = resp.data.Data;

          this.reset();

          if (data && data.ComponentBalances) {
            data.ComponentBalances.forEach(compBal => {
              compBal.CodeBalances.forEach(codeBal => {
                switch (codeBal.LeaveCode) {
                  case "07":
                    this.add(this.sick, codeBal);
                    break;
                  case "09":
                    this.add(this.annual, codeBal);
                    break;
                  case "10":
                    this.add(this.longservice, codeBal);
                    break;
                }
              });
            });
          }
        })
        .catch(err => {
          this.error = "Could not load leave balances.";
        })
        .then(() => {
          this.loading = false;
        });
    },
    computed: {
      showWidget() {
        return (
                this.loading ||
                this.annual.show ||
                this.sick.show ||
                this.longservice.show
        );
      }
    },
    methods: {
      reset() {
        this.annual = new Leave("Annual Leave");
        this.longservice = new Leave("Long Service Leave");
        this.sick = new Leave("Personal Leave");
      },
      add(leave, data) {
        leave.title = data.CodeDescription;
        leave.date = Date.parse(data.PeriodEndDate).format("%a %e %b %Y");
        leave.jsDate = Date.parse(data.PeriodEndDate)
        leave.days += data.TotalDays;
        leave.hours += data.TotalHours;
        leave.entitled += data.Entitlement;
        leave.show = true;
        if (data.IncludeFutureLeave) {
          leave.subText = "inc. future leave";
        } else {
          leave.subText = "exc. future leave";
        }
      },
      floor(value) {
        return Math.floor(value);
      },

      getLeaveWithDate(jsDate, code) {
        let date = moment(jsDate).format('DD-MMM-YYYY');
        Remote.LeaveBalanceWithDate(date, code)
                .then(res => {
                  let codeBal = res.data.Data.ComponentBalances[0].CodeBalances[0];
                  switch (code) {
                    case "07":
                      this.sick.days = codeBal.TotalDays;
                      this.sick.hours = codeBal.TotalHours;
                      break;
                    case "09":
                      this.annual.days = codeBal.TotalDays;
                      this.annual.hours = codeBal.TotalHours;
                      break;
                    case "10":
                      this.longservice.days = codeBal.TotalDays;
                      this.longservice.hours = codeBal.TotalHours;
                      break;
                  }
                })
      }
    },

  };
</script>

<template>
  <tile v-if="showWidget"
        icon="icon-plane"
        title="My Leave"
        class="my-leave-widget"
        :loading="loading">
    <div v-if="error">{{ error }}</div>
    <div v-else>
      <div v-if="annual.show" class="detailRow green">
        <div class="numTile">
          <div class="style"></div>
          <div class="value">{{ floor(annual.days) }}</div>
        </div>
        <div class="numValue">
          <div class="title">{{ annual.title }}</div>
          <div class="filter-item">
            <span class="filter-label mr-2">Your available Leave on</span>
            <span class="d-inline">
                <datepicker v-model="annual.jsDate" format="DD/MM/YYYY" @change="getLeaveWithDate(annual.jsDate, '09')"></datepicker>
                <span class="ui-calendar-buttons  mr-2">
                  <span class="ui-calendar-button button w-icon-only blue ui-has-tooltip"
                        data-calendar="history-date-from" data-tooltip="Open"
                        data-tooltip-dir="top">
                    <span></span>
                  </span>
                </span>
              </span>
            <span >is {{floor(annual.hours)}} hours</span>
          </div>
        </div>
      </div>
      <div v-if="longservice.show" class="detailRow blue">
        <div class="numTile">
          <div class="style"></div>
          <div class="value">{{ floor(longservice.days) }}</div>
        </div>
        <div class="numValue">
          <div class="title">{{ longservice.title }}</div>
          <div class="filter-item">
            <span class="filter-label mr-2">Your available Leave on</span>
            <span class="d-inline">
                <datepicker v-model="longservice.jsDate" format="DD/MM/YYYY"  @change="getLeaveWithDate(longservice.jsDate, '10')"></datepicker>
                <span class="ui-calendar-buttons  mr-2">
                  <span class="ui-calendar-button button w-icon-only blue ui-has-tooltip"
                        data-calendar="history-date-from" data-tooltip="Open"
                        data-tooltip-dir="top">
                    <span></span>
                  </span>
                </span>
              </span>
            <span >is {{floor(longservice.hours)}} hours</span>
          </div>
          <!--          <div class="hours">{{ floor(longservice.hours) }} hours as at {{ longservice.date }} ({{ longservice.subText }})</div>-->
        </div>
      </div>
      <div v-if="sick.show" class="detailRow orange">
        <div class="numTile">
          <div class="style"></div>
          <div class="value">{{ floor(sick.days) }}</div>
        </div>
        <div class="numValue">
          <div class="title">{{ sick.title }}</div>
          <div class="filter-item">
            <span class="filter-label mr-2">Your available Leave on</span>
            <span class="d-inline">
                <datepicker v-model="sick.jsDate" format="DD/MM/YYYY" @change="getLeaveWithDate(sick.jsDate, '7')"></datepicker>
                <span class="ui-calendar-buttons  mr-2">
                  <span class="ui-calendar-button button w-icon-only blue ui-has-tooltip"
                        data-calendar="history-date-from" data-tooltip="Open"
                        data-tooltip-dir="top">
                    <span></span>
                  </span>
                </span>
              </span>
            <span >is {{floor(sick.hours)}} hours</span>
          </div>
          <!--          <div-->
          <!--            class="hours"-->
          <!--          >{{ floor(sick.hours) }} hours as at {{ sick.date }} ({{ sick.subText }})</div>-->
        </div>
      </div>
    </div>
  </tile>
</template>

<style lang="scss">
  $leave-tile-width: 70px;
  $button-blue: #44b5ec;
  $button-green: #7abd2d;
  $button-yellow: #ffbd00;
  $button-orange: #ff6600;

  .my-leave-widget {
    .detailRow {
      clear: both;
      float: none;
      // height: 60px;
      margin: 0 0 5px 0;
      color: #666;
    }

    .detailRow .numTile {
      position: relative;
      float: left;
      width: $leave-tile-width;
      height: 60px;
      background-color: #fff;
      border-radius: 5px;
    }

    .detailRow .numTile .style {
      position: absolute;
      top: 29px;
      width: $leave-tile-width;
      height: 28px;
      border-top: 2px solid #f3f3f3;
    }

    .detailRow .numTile .value {
      position: absolute;
      top: 0;
      width: $leave-tile-width;
      height: 60px;
      line-height: 60px;
      font-size: 30px;
      text-align: center;
      letter-spacing: -3px;
    }

    .detailRow .numValue {
      margin-left: $leave-tile-width + 10px;
      margin-right: 10%;
    }

    .detailRow .numValue .title {
      /*height: 30px;
                      line-height: 40px;*/
      margin: 0;
      font-size: 16px;
      font-weight: normal;
    }

    .detailRow .numValue .hours {
      font-size: 12px;
      margin-top: 5px;
    }

    .detailRow .entitlement {
      float: right;
      width: 30%;
      /*margin: 15px 0 0 0;*/
      font-size: 12px;
      text-align: right;
      /*line-height: 20px;*/
    }

    .detailRow.blue .numTile .value {
      color: lighten($button-blue, 10%);
    }

    .detailRow.green .numTile .value {
      color: lighten($button-green, 10%);
    }
    .detailRow.orange .numTile .value {
      color: lighten($button-orange, 10%);
    }

    .datepicker {
      display: inline-block;
      width: 80px;

      input {
        width: 80px;
      }
    }
  }
</style>
